import Vue from 'vue';
import Vuex from 'vuex';

import currentUser from './modules/currentUser/currentUser';
import settings from './modules/settings/settings';
import totalCounts from './modules/totalCounts/totalCounts';
import activeTab from './modules/activeTab/activeTab';
import tabsData from './modules/tabsData/tabsData';
import clientStatus from './modules/clientStatus/clientStatus';
import infoMessage from './modules/infoMessage/infoMessage';

Vue.use(Vuex);

/* eslint-disable no-param-reassign */
export default new Vuex.Store({
  modules: {
    user: currentUser,
    settings,
    totalCounts,
    activeTab,
    tabsData,
    clientStatus,
    infoMessage,
  },
});



