/* eslint-disable */
import {
  SET_INFO_MESSAGE,
} from './mutation-types';

const state = {
  text: '',
  type: 'error',
};

const mutations = {
  [SET_INFO_MESSAGE](state, data) {
    state.text = data.text;
    if (data.type) { state.type = data.type };
    if (data.handleButton) { state.handleButton = data.handleButton };
    if (data.handleCheckbox) { state.handleCheckbox = data.handleCheckbox };
  },
};

const actions = {
  setInfoMessage({ commit }, data) {
    commit(SET_INFO_MESSAGE, data);
  },
};

export default {
  state,
  mutations,
  actions,
};



