import Vue from 'vue';
import Router from 'vue-router';
import platform from 'platform';

import Dashboard from '@/components/user/Dashboard';
import Integrity from '@/components/user/Integrity';
import Statistics from '@/components/user/Statistics';
import Settings from '@/components/user/Settings';

import Register from '@/components/auth/Register';
import Login from '@/components/auth/Login';
import Auth from '@/components/auth/Auth';

import local from '../api/local';
import piwik from '../api/piwik';


Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '',
      redirect: '/auth/login',
    },
    {
      path: '/auth',
      component: Auth,
      redirect: '/auth/login',
      children: [
        {
          path: 'login',
          component: Login,
          meta: { requiresGuest: true },
          name: 'Login',
        },
        {
          path: 'register',
          component: Register,
          meta: { requiresGuest: true },
          name: 'Register',
        },
      ],
    },
    {
      path: '/user',
      component: Dashboard,
      redirect: '/user/integrity',
      children: [
        {
          path: 'integrity',
          component: Integrity,
          name: 'Integrity',
        },
        {
          path: 'statistics',
          component: Statistics,
          name: 'Statistics',
        },
        {
          path: 'settings',
          component: Settings,
          name: 'Settings',
        },
      ],
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
});

// Send piwik data during router navigations if the user wants to send anonymous data
/* eslint-disable no-unused-vars */
router.afterEach((to, from) => {
  const piwikId = local.getPiwikId();
  if (piwikId) {
    piwik.track({
      url: `https://www.ovpnextension.com${to.path}`,
      action_name: to.name,
      _id: piwikId,
      _cvar: JSON.stringify({
        1: ['Platform', `${platform.name} ${platform.version} (${platform.description})`],
        2: ['Extension version', local.getVersion()],
        3: ['Client version', local.getClientVersion()],
        4: ['Language', navigator.language.split('-')[0]],
      }),
    });
  }
});

export default router;



