<template>
  <div class="register">
    <info-message
      v-if="errorMessage"
      :info="{
        type: 'error',
        text: errorMessage,
        handleButton: hideError,
      }">
    </info-message>
    <ovpn-logo
      :styleObj="{
        height: '80px',
        margin: '15px auto'
      }">
    </ovpn-logo>
    <div class="form" v-if='!isRegistered'>
      <form @submit.prevent="onSubmit">
        <div class="form__input">
          <input
            type="text"
            id="username"
            v-model="username"
            :placeholder="$t('register.username')"
            class="field">
        </div>
        <div class="form__input">
          <input
            type="email"
            id="email"
            v-model="email"
            :placeholder="$t('register.email')"
            class="field">
        </div>
        <div class="form__input">
          <input
            type="password"
            id="password"
            v-model="password"
            :placeholder="$t('register.password')"
            class="field">
        </div>
        <div class="form__input">
          <input
          type="password"
          id="confirmPassword"
          v-model="confirmPassword"
          :placeholder="$t('register.confirm')"
          class="field">
        </div>
        <button type="submit" class="btn-std btn-submit">
          <icon v-if="isSubmitting" name="circle-o-notch" spin></icon> {{ $t("common.submit") }}
        </button>
      </form>
    </div>
    <verify v-else :isSpinner="verifying" @startVerifying="handleVerifying"></verify>
  </div>
</template>

<script>
import 'vue-awesome/icons/circle-o-notch';
import Icon from 'vue-awesome/components/Icon';
import { mapActions } from 'vuex';
import InfoMessage from '../common/InfoMessage';
import OvpnLogo from '../common/Logo';
import Verify from './Verify';
import api from '../../api/ovpn-api';

export default {
  name: 'register',
  components: {
    InfoMessage,
    OvpnLogo,
    Verify,
    Icon,
  },
  mounted() {
    // Grab language from i18n
    this.language = this.$t('metadata.locale');
  },
  data() {
    return {
      isRegistered: false,
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      errorMessage: '',
      language: '',
      isSubmitting: false,
      verifying: false,
    };
  },
  methods: {
    ...mapActions({
      setCurrentUser: 'setCurrentUser',
    }),
    onSubmit() {
      this.isSubmitting = true;
      this.isError = '';

      const { username, email, password, confirmPassword, language } = this;
      api.register({
        username,
        email,
        password,
        confirmPassword,
        language,
      })
        .then(() => {
          this.isSubmitting = false;
          this.isRegistered = true;
        })
        .catch((err) => {
          this.isSubmitting = false;
          this.errorMessage = err.message;
        });
    },
    handleVerifying() {
      this.verifying = true;
      api.login(this.username, this.password)
        .then((res) => {
          this.verifying = false;
          if (res.active) {
            this.setCurrentUser(res);
            this.$router.push('/user');
          } else {
            this.errorMessage = this.$t('error.not-verified');
          }
        })
        .catch((err) => {
          this.verifying = false;
          this.errorMessage = err.message;
        });
    },
    hideError() {
      this.errorMessage = '';
    },
  },
};
</script>

<style scoped>
  .fa-icon {
    width: auto;
    height: 1em;
  }
</style>



