<template>
  <div id="app" class="app-container">
    <info-message v-if="infoMessage.text" :info="infoMessage"></info-message>
    <router-view></router-view>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import Dashboard from './components/user/Dashboard';
import InfoMessage from './components/common/InfoMessage';
import local from './api/local';
import settingsList from './data/settingsList';

export default {
  name: 'app',
  components: {
    Dashboard,
    InfoMessage,
  },
  mounted() {
    this.checkUserInLocalStorage();
    this.checkCountsInLocalStorage();
    this.setStartSettings(settingsList);
    this.setMessageListeners();
    this.getAndSetActiveTab();
    this.getAndSetTabsData();
    this.getClientStatus();
  },
  computed: {
    ...mapState({
      user: 'user',
      infoMessage: 'infoMessage',
      settings: 'settings',
      clientStatus: 'clientStatus',
    }),
  },
  methods: {
    ...mapActions({
      setCurrentUser: 'setCurrentUser',
      clearCurrentUser: 'clearCurrentUser',
      setStartSettings: 'setStartSettings',
      updateCounts: 'updateCounts',
      setActiveTab: 'setActiveTab',
      setTabsData: 'setTabsData',
      setClientStatus: 'setClientStatus',
      setInfoMessage: 'setInfoMessage',
    }),
    checkUserInLocalStorage() {
      const user = local.getUser();
      if (user) {
        this.$router.push('/user');
        this.setCurrentUser(user);
      }
    },
    getAndSetActiveTab() {
      browser.tabs.query({ active: true, currentWindow: true }, (tabs) => {
        const data = {
          id: tabs[0].id,
          url: tabs[0].url,
        };
        this.setActiveTab(data);
      });
    },
    getAndSetTabsData() {
      const tabsCounts = local.getTabsCounts();
      this.setTabsData(tabsCounts);
    },
    checkCountsInLocalStorage() {
      const counts = local.getTotalCounts();
      if (counts) {
        this.updateCounts(counts);
      }
    },
    /* eslint-disable no-unused-vars */
    setMessageListeners() {
      browser.runtime.onMessage.addListener(
        (request, sender, sendResponse) => {
          if (request.countChanged) {
            this.updateCounts(request.countChanged);
          } else if (request.tabCountsChanged) {
            this.setTabsData(request.tabCountsChanged);
          } else if (request.adsListChanged) {
            this.setTabsData(request.adsListChanged);
          } else if (request.trackersListChanged) {
            this.setTabsData(request.trackersListChanged);
          } else if (request.clientStatus) {
            this.setClientStatus(request.clientStatus);
          } else if (request.userFromClient) {
            this.setCurrentUser(request.userFromClient);
            this.$router.push('/user');
          }
        });
    },
    getClientStatus() {
      browser.runtime.sendMessage({ clientStatus: true }, (response) => {
        this.setClientStatus(response);
      });
    },
  },
};
</script>

<style>
  .app-container {
    width: 375px;
    height: 420px;
  }

  .content-wrapper {
    width: 355px;
    height: 360px;
    margin: 10px auto 0;
  }

  body {
    font-size:13px;
    height: 100%;
    -webkit-font-smoothing: antialiased;
  }

  button:focus {
    outline: none;
  }

  .btn-std {
    padding: 7px 10px;
    font-weight: bold;
    color: #fff;
    background-color: #283d59;
    border-radius: 7px;
    border: 1px solid transparent;
    transition: background 0.5s ease;
  }

  .btn-std:hover, .btn-std:focus, .btn-std:active {
    background-color: #283d59;
    color: #fff;
  }

  .btn-inv {
    border: 1px solid #ccc;
    background-color: transparent;
    color: #283D59;
  }

  .btn-inv:hover, .btn-inv:focus, .btn-inv:active {
    border-color: transparent;
    background: #283D59;
    color: #fff;
  }

  .btn-inv:disabled {
    background-color: #f6f6f6;
    opacity: 0.5;
  }

  .btn-inv:disabled:hover, .btn-inv:disabled:focus {
    border: 1px solid #283D59;
    color: #283D59;
    cursor: not-allowed;
  }

  a.btn-std:hover, a.btn-std:focus, a.btn-std:active {
    text-decoration: none;
  }
</style>



