/* eslint-disable */
import {
  SET_ACTIVE_TAB,
} from './mutation-types';

const state = { id: 0, url: '' };

const mutations = {
  [SET_ACTIVE_TAB](state, data) {
    state.id = data.id;
    state.url = data.url;
  },
};

const actions = {
  setActiveTab({ commit }, data) {
    commit(SET_ACTIVE_TAB, data);
  },
};

export default {
  state,
  mutations,
  actions,
};



