<template>
  <div class="dashboard">
    <app-nav>
      <nav-link linkPath="/user/integrity" linkTitle="nav.integrity"></nav-link>
      <nav-link linkPath="/user/statistics" linkTitle="nav.statistics"></nav-link>
      <nav-link linkPath="/user/settings" linkTitle="nav.settings"></nav-link>
    </app-nav>
    <div class="content-wrapper">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import AppNav from '../common/Nav';
import NavLink from '../common/NavLink';

export default {
  name: 'dashboard',
  components: {
    AppNav,
    NavLink,
  },
};
</script>



