<template>
  <div class="integrity">
    <service-controls v-if="shortUrl"
      @onWhitelistClick='handleWhitelistClick'
      @onForceClick='handleForceClick'
      :isSiteWhitelisted="isSiteWhitelisted"
      :isSiteForced="isSiteForced"
      :shortUrl="shortUrl"
    ></service-controls>
    <div v-if="!shortUrl" class="top-holder"></div>

    <service-list></service-list>

    <div v-if="isConnected" class="status">
      <connection-info
        :ipData="{ip, ipV6: ovpnObject.ipV6}"
        :serverData="server"
        :isTCP="ovpnObject.tcp"
        :isLoading="isDataLoading"
      ></connection-info>
    </div>
    <div v-if="!isConnected" class="status">
      <div class="status-message">
        <div :class="['status-circle', statusClass]"></div>
        <span v-if="statusClass === 'stopped'" class="status-text">
          {{ $t('account.stopped') }}
        </span>
        <span v-if="statusClass === 'closed'" class="status-text">
          {{ $t('account.closed') }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import R from 'ramda';
import { mapState } from 'vuex';
import ConnectionInfo from './ConnectionInfo';
import ServiceControls from './ServiceControls';
import ServiceList from './ServiceList';
import local from '../../api/local';
import helpers from '../../helpers/helpers';
import api from '../../api/ovpn-api';

export default {
  name: 'integrity',
  components: {
    ConnectionInfo,
    ServiceList,
    ServiceControls,
  },
  mounted() {
    this.checkCurrentUrlStatus();
    this.getOvpnObject();
    if (this.isConnected) {
      this.getIpData();
    }
  },
  data() {
    return {
      ovpnObject: {},
      isSiteWhitelisted: false,
      isSiteForced: false,
      currentTabUrl: '',
      ip: '',
      server: {
        name: '',
        country: '',
      },
      isDataLoading: false,
    };
  },

  computed: {
    ...mapState({
      clientStatus: 'clientStatus',
    }),

    shortUrl() {
      return this.currentTabUrl ? this.currentTabUrl : '';
    },

    isConnected() {
      return this.clientStatus.status === 'connected';
    },

    statusClass() {
      const status = this.clientStatus.status;
      if (status === 'closed') {
        return 'closed';
      } else if (status === 'connected') {
        return 'connected';
      }
      return 'stopped';
    },

    ipv6() {
      return (this.ovpnObject && this.ovpnObject.ipV6 && this.ovpnObject.ipV6 !== 'None') ? this.ovpnObject.ipV6 : null;
    },
  },

  watch: {
    isConnected(status) {
      // get OvpnObject and make api calls on connected status:
      if (status) {
        this.getOvpnObject();
        this.getIpData();
      }
    },
  },

  methods: {
    checkCurrentUrlStatus() {
      browser.tabs.query({ currentWindow: true, active: true }, (tabs) => {
        this.currentTabUrl = helpers.cutUrl(tabs[0].url);
        const sitesWhitelist = local.getSitesWhitelist();
        this.isSiteWhitelisted = sitesWhitelist.includes(this.currentTabUrl);
        const forceList = local.getForceList();
        this.isSiteForced = forceList.includes(this.currentTabUrl);
      });
    },
    handleWhitelistClick() {
      const sitesWhitelist = local.getSitesWhitelist();
      let updatedSitesWhitelist = [...sitesWhitelist];
      if (!this.isSiteWhitelisted) {
        updatedSitesWhitelist = [...sitesWhitelist, this.currentTabUrl];
      } else {
        const siteIndex = sitesWhitelist.indexOf(this.currentTabUrl);
        updatedSitesWhitelist = [...sitesWhitelist.slice(0, siteIndex),
          ...sitesWhitelist.slice(siteIndex + 1)];
      }

      local.setSitesWhitelist(updatedSitesWhitelist);
      this.isSiteWhitelisted = !this.isSiteWhitelisted;
    },

    handleForceClick() {
      const forceList = local.getForceList();
      if (this.isSiteForced) {
        local.setForceList(R.without([this.currentTabUrl], forceList));
      } else {
        local.setForceList(R.append(this.currentTabUrl, forceList));
      }
      this.isSiteForced = !this.isSiteForced;
    },

    getIpData() {
      const cachedIp = local.getIpData();

      if (cachedIp) {
        this.ip = cachedIp;
      } else {
        api.getIpData().then((res) => {
          this.ip = res.data.ip;
          local.setIpData(res.data.ip);
        })
        .catch((err) => {
          console.log('err', err.message);
        });
      }
    },

    getServerData() {
      const cachedData = local.getServerData();

      if (cachedData) {
        this.server.name = cachedData.name;
        this.server.country = cachedData.country;
      } else {
        this.isDataLoading = true;
        api.getServerData(this.ovpnObject.serverIp).then((res) => {
          this.isDataLoading = false;
          this.server.name = res.data.name;
          this.server.country = res.data.country;
          local.setServerData(res.data);
        })
        .catch((err) => {
          this.isDataLoading = false;
          console.log('err', err.message);
        });
      }
    },

    getOvpnObject() {
      browser.runtime.sendMessage({ ovpnObject: true }, (response) => {
        this.ovpnObject = response;
        if (this.isConnected) {
          this.getServerData();
        }
      });
    },
  },
};
</script>

<style scoped>
.integrity {
  height: 100%;
  position: relative;
}

.top-holder {
  height: 70px;
}

.status {
  height: 50px;
  margin-left: -10px;
  margin-right: -10px;
  margin-top: 10px;
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center;
}

.status-message {
  color: #333;
  display: flex;
  padding: 10px 20px;
  justify-content: center;
  align-items: center;
}

.status-circle {
  height: 15px;
  width: 15px;
  margin-right: 5px;
  border-radius: 50%;
  position: relative;
  display: inline-block;
}

.stopped {
  background: #555;
}

.closed {
  background: #c55;
}

.status-text {
  font-size: 16px;
}
</style>



