import Vue from 'vue';
import VueI18n from 'vue-i18n';
import Raven from 'raven-js';
import RavenVue from 'raven-js/plugins/vue';
import ToggleButton from 'vue-js-toggle-button';
import App from './App';
import localeEn from './i18n/en';
import localeSv from './i18n/sv';
import router from './router';

import store from './store/store';
import local from './api/local';

window.browser = window.msBrowser || window.browser || window.chrome;

Vue.use(ToggleButton);
Vue.use(VueI18n);

const language = window.navigator.userLanguage || window.navigator.language;
const shortLang = language.split('-')[0];

const messages = {
  en: localeEn,
  sv: localeSv,
};

const i18n = new VueI18n({
  locale: shortLang,
  fallbackLocale: 'en',
  messages,
});

Vue.config.productionTip = false;

// Configure Raven and add the RavenVue plugin
Raven.config('https://2302dd1785ad4decbe64b753b10de3d6@sentry.ovpn.se/38')
.addPlugin(RavenVue, Vue);

const settings = local.getSettings();

if (settings && settings.sendData.checked) {
  Raven.install();
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  i18n,
  router,
  store,
  render: h => h(App),
});



