<template>
  <div class="app-nav">
    <div class="app-nav__wrapper" :style="{ width: navWidth + 'px' }">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'appnav',
  props: {
    navWidth: {
      type: Number,
      default: 275,
    },
  },
};
</script>

<style scoped>
.app-nav {
  background-color: #283D59;
  display: flex;
  justify-content: center;
  height: 50px;
}

.app-nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>



