import { Doughnut } from 'vue-chartjs';

export default Doughnut.extend({
  props: ['data', 'options', 'labels'],
  mounted() {
    this.renderChart(
      // Data
      {
        labels: this.labels,
        datasets: [{
          data: this.data,
          backgroundColor: [
            'rgba(18, 59, 94, 0.5)',
            'rgba(108, 180, 241, 0.5)',
          ],
          borderColor: [
            '#fff',
            '#fff',
          ],
          borderWidth: 1,
        }],
      },
      // Options
      {
        responsive: false,
        legend: {
          display: true,
          position: 'bottom',
          labels: {
            fontColor: '#283d59',
          },
        },
        ...this.options,
      });
  },
});



