<template>
  <div class="lists">

    <h5 v-if="isTabExist && isListNotEmpty" class="title">
      {{ $t('integrity.rules') }}
    </h5>

    <div v-if="isTabExist && isListNotEmpty" class="service-list">
      <service-item
        v-for="service in fullList"
        :key="service.title"
        :service="service"
        @changeWhitelistDomain="handleWhitelistDomainChange(service)"
      ></service-item>
    </div>

    <div v-else class="message">
      <p>{{ $t('integrity.empty') }}</p>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';
import ServiceItem from './ServiceItem';
import local from '../../api/local';

export default {
  name: 'service-list',
  components: {
    ServiceItem,
  },
  data() {
    return {
      domainsWhitelist: [],
    };
  },
  mounted() {
    this.getDomainsWhitelist();
  },
  computed: {
    ...mapState({
      activeTab: 'activeTab',
      tabsData: 'tabsData',
    }),
    adsList() {
      return this.tabsData.tabs[this.activeTab.id].adsList || [];
    },
    trackersList() {
      return this.tabsData.tabs[this.activeTab.id].trackersList || [];
    },
    fullList() {
      const adLabel = this.$t('integrity.ad');
      const trackerLabel = this.$t('integrity.tracker');
      const updatedAdsList = this.adsList.map((ad) => {
        const isDomainWhitelisted = this.domainsWhitelist.includes(ad);
        return { title: ad, label: adLabel, checked: isDomainWhitelisted };
      });
      const updatedTrackersList = this.trackersList.map((tracker) => {
        const isDomainWhitelisted = this.domainsWhitelist.includes(tracker);
        return { title: tracker, label: trackerLabel, checked: isDomainWhitelisted };
      });
      return updatedAdsList.concat(updatedTrackersList);
    },
    isTabExist() {
      return this.tabsData.tabs[this.activeTab.id];
    },
    isListNotEmpty() {
      return !!this.fullList.length;
    },
  },
  methods: {
    getDomainsWhitelist() {
      this.domainsWhitelist = local.getDomainsWhitelist();
    },
    handleWhitelistDomainChange(service) {
      console.log('handleWhitelistDomainChange', service);
      if (service.checked) {
        const domainIndex = this.domainsWhitelist.indexOf(service.title);
        if (domainIndex >= 0) {
          this.domainsWhitelist.splice(domainIndex, 1);
          local.setDomainsWhitelist(this.domainsWhitelist);
        }
      } else {
        this.domainsWhitelist.push(service.title);
        local.setDomainsWhitelist(this.domainsWhitelist);
      }
    },
  },
};
</script>

<style scoped>
  .lists .title {
    margin: 25px 0 5px;
    font-size: 16px;
    text-align: center;
    height: 20px;
  }

  .service-list {
    height: 180px;
  }

  .message {
    height: 230px;
  }

  .service-list {
    overflow-y: scroll;
  }

  .message {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .message p {
    font-size: 18px;
  }
</style>



