/* eslint-disable */
import {
  SET_CLIENT_STATUS,
} from './mutation-types';

const state = {
  status: '',
};

const mutations = {
  [SET_CLIENT_STATUS](state, status) {
    state.status = status;
  },
};

const actions = {
  setClientStatus({ commit }, status) {
    commit(SET_CLIENT_STATUS, status);
  },
};

export default {
  state,
  mutations,
  actions,
};



