<template>
  <div :class="['message', info.type]">
    <h3>{{ $t("common." + info.type) }}:</h3><p>{{ info.text }}</p>
    <button class="btn-message" @click="handleButton">OK</button>
    <label v-if="info.handleCheckbox">
      <input type="checkbox" @change="info.handleCheckbox" />
      {{ $t('error.session-checkbox') }}
    </label>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  props: {
    info: {
      type: Object,
    },
  },
  methods: {
    ...mapActions({
      setInfoMessage: 'setInfoMessage',
    }),
    handleButton() {
      if (this.info.handleButton) {
        return this.info.handleButton();
      }
      return this.setInfoMessage({ text: '' });
    },
  },
};
</script>

<style scoped>
  .message {
    z-index: 10;
    padding: 5px 15px;
    margin-bottom: 20px;
    width: 295px;
    height: 290px;
    position: absolute;
    border-radius: 5px;
    font-weight: 700;
    vertical-align: middle;
    left: 40px;
    top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .btn-message {
    margin-top: 30px;
    background-color: transparent;
    border-radius: 5px;
    padding: 7px 40px;
  }

  h3 {
    text-align: center;
    padding: 0 10px;
    margin-top: 30px
  }

  /* error type */
  .error {
    background-color: #ffeeee;
  }
  .error h3 {
    color: #ff3333;
  }
  .error .btn-message {
    color: #d9534f;
    border: #d9534f 1px solid;
  }
  .error .btn-message:hover {
    color: #fff;
    background-color: #d9534f;
  }

  /* warning type */
  .warning {
    background-color: #f9f1b5;
  }
  .warning h3 {
    color: #d2c950;
  }
  .warning .btn-message {
    color: #d2c950;
    border: #ee9c21 1px solid;
  }
  .warning .btn-message:hover {
    color: #fff;
    background-color: #d2c950;
  }
</style>



