<template>
  <p v-if="isSubscription" class="subscription">
    {{ $t('account.until') }}:
    <span v-if="days > 1">{{ days }} {{ $t('account.days') }}</span>
    <span v-if="days === 1">{{ days }} {{ $t('account.day') }}</span>
    <span v-if="days === 0 && hours > 1">{{ hours }} {{ $t('account.hours') }}</span>
    <span v-if="days === 0 && hours <= 1">{{ hours }} {{ $t('account.hour') }}</span>
  </p>
  <p v-else class="subscription">
    {{ $t('account.no-subscription') }} -
    <a href="https://www.ovpn.com/en/pricing" target="_blank">
      {{ $t('account.buy') }}
    </a>
  </p>
</template>

<script>
import moment from 'moment';

export default {
  name: 'subscription-info',
  props: {
    isSubscription: Number,
  },
  computed: {
    days() {
      if (!this.isSubscription) { return null; }
      const diff = moment.unix(this.isSubscription) - moment();
      return Math.floor(moment.duration(diff).asDays());
    },
    hours() {
      if (!this.isSubscription) { return null; }
      const diff = moment.unix(this.isSubscription) - moment();
      return moment.duration(diff).hours();
    },
  },
};
</script>

<style scoped>
  .subscription {
    margin-bottom: 15px;
    margin-top: 10px;
    font-size: 12px;
    color: #6f6f6f;
  }
</style>



