<template>
  <div class="service-controls">
    <div v-if="serviceMessage" class="service-message">{{ serviceMessage }}</div>
    <div class="service-controls__site">
      <h5 class="title">{{ shortUrl }}</h5>
    </div>
    <div class="service-controls__buttons">
      <div class="control__block">
        <toggle-button
          class="domain-toggle"
          @change="onWhitelistClick"
          width="90"
          height="30"
          color="#283d59"
          :value="!isSiteWhitelisted"
          :sync="true"
          :disabled="!shortUrl"
          :labels="{checked: $t('integrity.block'), unchecked: $t('integrity.block')}"/>
      </div>

      <!-- Remove v-if when bypass functionality will be implemented -->
      <button v-if="false" class="btn-std btn-inv" disabled>{{ $t('integrity.bypass') }}</button>

      <div class="control__block">
        <toggle-button
          class="domain-toggle"
          @change="onForceClick"
          width="90"
          height="30"
          color="#283d59"
          :value="isSiteForced"
          :sync="true"
          :disabled="!shortUrl"
          :labels="{checked: $t('integrity.force'), unchecked: $t('integrity.force')}"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'controls',
  props: {
    isSiteWhitelisted: {
      type: Boolean,
    },
    isSiteForced: {
      type: Boolean,
    },
    shortUrl: {
      type: String,
    },
  },
  data() {
    return {
      serviceMessage: '',
    };
  },
  methods: {
    onWhitelistClick() {
      this.$emit('onWhitelistClick');
      this.serviceMessage = this.isSiteWhitelisted ?
        this.$t('integrity.whitelist-off') :
        this.$t('integrity.whitelist-on');
      this.clearMessage();
    },
    onForceClick() {
      this.$emit('onForceClick');
      this.serviceMessage = this.isSiteForced ?
        this.$t('integrity.force-off') :
        this.$t('integrity.force-on');
      this.clearMessage();
    },
    clearMessage() {
      setTimeout(() => {
        this.serviceMessage = '';
      }, 2000);
    },
  },
};
</script>

<style scoped>
  .service-controls {
    height: 70px;
    position: relative;
  }

  .service-message {
    position: absolute;
    height: 20px;
    width: 100%;
    background: #d1ecf1;
    top: -10px;
    width: 375px;
    margin-left: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .service-controls__buttons {
    display: flex;
    justify-content: space-around;
    margin-top: 15px;
  }

  .btn-inv {
    width: 100px;
  }

  .service-controls__site {
    height: 30px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
  }

  .service-controls__site .title {
    margin: 0;
    font-size: 16px;
    text-align: center;
  }

  .vue-js-switch.domain-toggle, .vue-js-switch.domain-toggle {
    font-size: 12px;
  }
</style>



