/* eslint-disable */

import {
  UPDATE_COUNTS,
} from './mutation-types';

const state = { ads: 0, trackers: 0, };

const mutations = {
  [UPDATE_COUNTS](state, counts) {
    state.ads = counts.ads;
    state.trackers = counts.trackers;
  },
};

const actions = {
  updateCounts({ commit }, counts) {
    commit(UPDATE_COUNTS, counts);
  },
};

export default {
  state,
  mutations,
  actions,
};



