<template>
  <div class="login">
    <info-message
      v-if="errorMessage"
      :info="{
        type: 'error',
        text: errorMessage,
        handleButton: hideError,
      }">
    </info-message>
    <ovpn-logo
      :styleObj="{
        height: '120px',
        margin: '30px auto 20px'
      }">
    </ovpn-logo>
    <div class="form">
      <form @submit.prevent="onSubmit">
        <div class="form__input">
          <input
            type="text"
            id="username"
            v-model="username"
            :placeholder="$t('login.username')"
            class="field">
        </div>
        <div class="form__input">
          <input
            type="password"
            id="password"
            v-model="password"
            :placeholder="$t('login.password')"
            class="field">
        </div>
        <button type="submit" class="btn-std btn-submit">
          <icon v-if="isSubmitting" name="circle-o-notch" spin></icon> {{ $t("common.submit") }}
        </button>
        <div class="reset">
          <a href="https://www.ovpn.com/account/forgot/" target="_blank">{{ $t("login.reset-password") }}</a>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import 'vue-awesome/icons/circle-o-notch';
import Icon from 'vue-awesome/components/Icon';
import { mapActions } from 'vuex';
import InfoMessage from '../common/InfoMessage';
import OvpnLogo from '../common/Logo';
import api from '../../api/ovpn-api';

export default {
  name: 'login',
  components: {
    InfoMessage,
    OvpnLogo,
    Icon,
  },
  data() {
    return {
      username: '',
      password: '',
      errorMessage: '',
      isSubmitting: false,
    };
  },
  methods: {
    ...mapActions({
      setCurrentUser: 'setCurrentUser',
    }),
    onSubmit() {
      this.isSubmitting = true;

      api.login(this.username, this.password)
        .then((res) => {
          this.isSubmitting = false;
          this.setCurrentUser(res);
          this.$router.push('/user');
        })
        .catch((err) => {
          this.isSubmitting = false;
          this.resetForm();
          this.errorMessage = err.message;
        });
    },
    resetForm() {
      this.username = '';
      this.password = '';
    },
    hideError() {
      this.errorMessage = '';
    },
  },
};
</script>

<style scoped>
  .login {
    display: flex;
    flex-direction: column;
  }

  .reset {
    margin-top: 5px;
  }

  .reset a {
    color: #3e3e3e;
    font-size: 12px;
  }

  .fa-icon {
    width: auto;
    height: 1em;
  }
</style>



