<template>
  <div class="stats">
    <div class="stats-container">
      <div class="stats-item stats-item--wide">
        <h5 class="tab-title">{{ $t('statistics.total') }}:</h5>
        <div>
          <span class="stat-title">{{ $t('statistics.ads-blocked') }}:</span>
          <span class="stat-data">{{ counts.ads }}</span>
        </div>
        <div>
          <span class="stat-title">{{ $t('statistics.trackers-blocked') }}:</span>
          <span class="stat-data">{{ counts.trackers }}</span>
        </div>
        <div>
          <span class="stat-title">{{ $t('statistics.average-ads') }}:</span>
          <span class="stat-data">{{ avgAdsPerDay }}</span>
        </div>
        <div>
          <span class="stat-title">{{ $t('statistics.average-trackers') }}:</span>
          <span class="stat-data">{{ avgTrackersPerDay }}</span>
        </div>
      </div>
      <div class="stats-item">
        <stats-chart
          :labels="labels"
          :data="totalCountsData"
          :options="{
            animation: {
              duration: 1500,
              easing: 'easeOutCubic',
            },
          }"
          :width="140"
          :height="160">
        </stats-chart>
      </div>
    </div>

    <div v-if="shortTabUrl" class="stats-container">
      <div class="stats-item stats-item--wide">
        <h5 class="tab-title">{{ shortTabUrl }}</h5>
        <div>
          <span class="stat-title">{{ $t('statistics.ads-blocked') }}:</span>
          <span class="stat-data">{{ pageCountsAds }}</span>
        </div>
        <div>
          <span class="stat-title">{{ $t('statistics.trackers-blocked') }}:</span>
          <span class="stat-data">{{ pageCountsTrackers }}</span>
        </div>
      </div>
      <div class="stats-item" v-if="isNotBlockedReq">
        <stats-chart
          :labels="labels"
          :data="pageCountsData"
          :width="140"
          :height="160">
        </stats-chart>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import StatsChart from './charts/StatsChart';
import h from '../../helpers/helpers';
import local from '../../api/local';

export default {
  name: 'statistics',
  components: {
    StatsChart,
  },
  data() {
    return {
      daysFromStart: null,
      labels: [
        this.$t('statistics.label-ads'),
        this.$t('statistics.label-trackers'),
      ],
    };
  },
  mounted() {
    const startDate = local.getStartDate();
    const now = Date.now();
    const diff = Math.ceil((now - startDate) / (1000 * 3600 * 24));
    this.daysFromStart = diff;
  },
  computed: {
    ...mapState({
      counts: 'totalCounts',
      activeTab: 'activeTab',
      tabsData: 'tabsData',
    }),
    totalCountsData() {
      return [this.counts.ads, this.counts.trackers];
    },
    pageCountsAds() {
      return this.tabsData.tabs[this.activeTab.id].ads;
    },
    pageCountsTrackers() {
      return this.tabsData.tabs[this.activeTab.id].trackers;
    },
    pageCountsData() {
      return [this.pageCountsAds, this.pageCountsTrackers];
    },
    shortTabUrl() {
      return h.cutUrl(this.activeTab.url);
    },
    isNotBlockedReq() {
      return this.pageCountsAds + this.pageCountsTrackers;
    },
    avgAdsPerDay() {
      return (this.counts.ads / this.daysFromStart).toFixed(2);
    },
    avgTrackersPerDay() {
      return (this.counts.trackers / this.daysFromStart).toFixed(2);
    },
  },
};
</script>

<style scoped>
  .stats {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 360px;
  }

  .stats-container {
    border-radius: 5px;
    display: flex;
    height: 167px;
    width: 100%;
    margin-bottom: 10px;
  }

  .stats-item {
    height: 165px;
    padding: 5px;
    border-radius: 5px;
  }

  .stats-item--wide {
    width: 215px;
    padding-left: 15px;
  }

  .stat-title {
    color: #283d59;
  }

  .stat-data {
    font-weight: bold;
  }

  .tab-title {
    font-size: 18px;
  }
</style>



