<template lang="html">
  <div class="connection-info">
    <loading-info v-if="isLoading"></loading-info>
    <div class="server-info" v-if="!isLoading">
      <div :class="['flag-icon-background', 'server-icon', flagClass]"></div>
      <div class="server-details">
        <div class="server-name">
          <span>{{ `${serverData.name} ${tcpText}` }}</span>
        </div>
        <div>{{ ipData.ip }}</div>
        <div>{{ ipData.ipV6 }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingInfo from '../common/Loading';
import '../../assets/css/flag-icon.css';

export default {
  name: 'server-info',

  components: {
    LoadingInfo,
  },

  props: {
    isLoading: {
      type: Boolean,
      required: true,
    },
    ipData: {
      type: Object,
      required: true,
    },
    serverData: {
      type: Object,
      required: true,
    },
    isTCP: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    flagClass() {
      return this.serverData.country ? `flag-icon-${this.serverData.country.toLowerCase()}` : '';
    },
    tcpText() {
      return this.isTCP ? '(TCP)' : '(UDP)';
    },
  },
};
</script>

<style scoped>
  .server-info {
    display: flex;
    background-color: #f6f6f6;
    height: 50px;
    padding: 5px;
    font-size: 10px;
  }

  .server-icon {
    width: 50px;
    height: 40px;
    margin-top: 2px;
    margin-left: 5px;
  }

  .server-name {
    font-weight: bold;
    display: flex;
    align-items: center;
  }

  .server-details {
    margin-left: 15px;
  }
</style>



