<template>
  <div class="auth">
    <app-nav navWidth="200">
      <nav-link linkPath="/auth/login" linkTitle="nav.login"></nav-link>
      <nav-link linkPath="/auth/register" linkTitle="nav.register"></nav-link>
    </app-nav>
    <div class="content-wrapper">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import AppNav from '../common/Nav';
import NavLink from '../common/NavLink';

export default {
  name: 'auth',
  components: {
    AppNav,
    NavLink,
  },
};
</script>

<style>
  .auth {
    text-align: center;
  }

  .btn-submit {
    margin-top: 5px;
    width: 300px;
  }

  .form__input {
    margin-bottom: 10px;
  }

  .field {
    width: 300px;
    height: 35px;
    line-height: 35px;
    background-color: #e6e6e6;
    border-radius: 7px;
    border: 0;
    padding-left: 30px;
  }

  .field::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #3e3e3e;
  }
  .field::-moz-placeholder { /* Firefox 19+ */
    color: #3e3e3e;
  }
  .field:-ms-input-placeholder { /* IE 10+ */
    color: #3e3e3e;
  }
  .field:-moz-placeholder { /* Firefox 18- */
    color: #3e3e3e;
  }
  .field::placeholder {
    color: #3e3e3e;
  }
</style>



