<template>
  <div class="settings__item">
    <div class="item__title">{{ $t('settings.' + option) }}</div>
    <div class="item__checkbox">
      <toggle-button
        width="30"
        height="15"
        @change="onChange"
        color="#283d59"
        :value="isChecked"
        :sync="true"/>
      <a :href="faqLinks[option][language]" target="_blank" class="faq-link">
        <icon name="question-circle" class="faq"></icon>
      </a>
    </div>
  </div>
</template>

<script>
import 'vue-awesome/icons/question-circle';
import Icon from 'vue-awesome/components/Icon';
import faqLinks from '../../data/faqLinks';

export default {
  name: 'settings-item',
  components: {
    Icon,
  },
  props: {
    option: {
      type: String,
      required: true,
    },
    isChecked: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      language: '',
      faqLinks,
    };
  },

  mounted() {
    this.setLanguage();
  },

  methods: {
    onChange() {
      const { option } = this;
      const value = !this.isChecked;
      this.$emit('set', { value, option });
    },
    setLanguage() {
      // Grab language from i18n
      const language = this.$t('metadata.locale');
      this.language = (language === 'en' || language === 'sv') ? language : 'en';
    },
  },
};
</script>

<style scoped>
  .settings__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #ddd;
    padding: 8px 15px 8px 15px;
  }

  .settings__item label {
    margin-bottom: 0;
  }

  .vue-js-switch {
    margin-left: 5px;
  }

  .settings__item:nth-child(odd) {
    background: #f9f9f9;
  }

  .item__checkbox {
    display: flex;
    align-items: center;
  }

  .item__checkbox input {
    margin-top: 2px;
  }

  .item__title {
    flex: 0 0 285px;
    font-weight: bold;
  }

  .faq {
    color: #459ce7;
    height: 11px;
    display: flex;
  }

  .faq-link {
    margin-left: 1px;
  }

</style>



