<template>
  <div class="settings">
    <settings-item
      v-for="(setting, key) in settingsList.list"
        :key="key"
        :option="key"
        :isChecked="setting.checked"
        @set="handleSet"
        >
    </settings-item>

    <div class="settings__footer">
      <div class="controls">
        <button
          @click="resync"
          class="btn-std btn-inv">
          {{ $t("account.resync") }}
        </button>
        <button
          @click="logout"
          class="btn-std btn-inv">
          {{ $t("login.logout") }}
        </button>
      </div>

      <SubscriptionInfo :isSubscription="isSubscription"></SubscriptionInfo>

      <div class="version">
        <span>{{ $t("account.version") }}.{{ version }}</span>
        <span v-if="ovpnObject.version" class="separator">|</span>
        <span v-if="ovpnObject.version">
          {{ $t("account.client-version") }}.{{ ovpnObject.version }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import SettingsItem from './SettingsItem';
import SubscriptionInfo from './SubscriptionInfo';
import local from '../../api/local';

export default {
  name: 'settings',

  components: {
    SettingsItem,
    SubscriptionInfo,
  },

  data() {
    return {
      ovpnObject: {},
      version: local.getVersion(),
    };
  },

  mounted() {
    this.getOvpnObject();
  },

  computed: {
    ...mapState({
      user: 'user',
      settingsList: 'settings',
    }),
    isSubscription() {
      return this.user.data.timestamp ? this.user.data.timestamp : 0;
    },
  },

  methods: {
    ...mapActions({
      updateSettings: 'updateSettings',
      clearCurrentUser: 'clearCurrentUser',
    }),

    handleSet(data) {
      this.updateSettings(data);
    },

    logout() {
      this.clearCurrentUser();
      this.$router.push('auth/login');
    },

    getOvpnObject() {
      browser.runtime.sendMessage({ ovpnObject: true }, (response) => {
        this.ovpnObject = response;
      });
    },

    resync() {
      console.log('Resync');
      browser.runtime.sendMessage({ resync: true });
    },
  },
};
</script>

<style scoped>
  .settings {
    height: 100%;
    position: relative;
  }

  .btn-std {
    width: 120px;
  }

  .settings__footer {
    text-align: center;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .controls {
    display: flex;
    justify-content: space-evenly;
  }

  .version {
    position: absolute;
    bottom: 0;
    color: #6f6f6f;
    font-size: 10px;
    text-align: center;
    left: 0;
    right: 0;
  }

  .separator {
    margin: 0 5px;
  }
</style>



