<template>
  <div class="loading">
    <h5>{{ $t("common.loading") }} ...</h5>
    <scale-loader color="#283D59" height="25px" width="4px"></scale-loader>
  </div>
</template>

<script>
import ScaleLoader from 'vue-spinner/src/ScaleLoader';

export default {
  name: 'loading',
  components: {
    ScaleLoader,
  },
};
</script>

<style scoped>
  .loading {
    display: flex;
    align-items: center;
  }

  h5 {
    margin-right: 10px;
  }
</style>



