/* eslint-disable */
import local from '../../../api/local';
import chromeHelpers from '../../../api/chrome';

import {
  SET_CURRENT_USER,
  CLEAR_CURRENT_USER,
} from './mutation-types';

const state = {
  data: {},
};

const mutations = {
  [SET_CURRENT_USER](state, user) {
    state.data = user;
  },
  [CLEAR_CURRENT_USER](state) {
    state.data = {};
  },
};

const actions = {
  setCurrentUser({ commit }, user) {
    local.setUser(user);
    chromeHelpers.sendMessage({currentUser: user});
    commit(SET_CURRENT_USER, user);
  },
  clearCurrentUser({ commit }) {
    local.clearUser();
    commit(CLEAR_CURRENT_USER);
    chromeHelpers.sendMessage({logout: true});
  },
};

export default {
  state,
  mutations,
  actions,
};



