<template>
  <div class="verify">
    <h2 class="title">{{ $t("verifying.title") }}</h2>
    <div class="message">
      {{ $t("verifying.message") }}
    </div>
    <button class="btn-std btn-verify" @click="$emit('startVerifying')">
      <scale-loader :loading="isSpinner" color="#ffffff" width="4px" height="30px"></scale-loader>
      <span>{{ btnText }}</span>
    </button>
  </div>
</template>

<script>
import ScaleLoader from 'vue-spinner/src/ScaleLoader';

export default {
  name: 'verify',
  components: {
    ScaleLoader,
  },
  props: {
    isSpinner: {
      type: Boolean,
    },
  },
  computed: {
    btnText() {
      return this.isSpinner ? this.$t('common.verifying') : this.$t('common.trial');
    },
  },
};
</script>

<style scoped>
  .message {
    text-align: left;
  }

  .title {
    font-size: 1.75em;
  }

  .btn-verify {
    margin-top: 15px;
    padding: 7px 15px;
  }
</style>



