<template>
  <div class="service-item">
    <div class="service-info">
      <div v-if="service.label" class="label" :style="{ backgroundColor: labelColor}">{{ service.label }}</div>
      <div class="service-info__title">{{ service.title }}</div>
    </div>
    <div class="service__toggle"
      :data-balloon-visible="isTooltipVisible"
      :data-balloon="tooltipText"
      data-balloon-pos="left">

      <toggle-button id="force"
        width="73"
        height="20"
        @change="onChange"
        color="#283d59"
        :value="!service.checked"
        :sync="true"
        :labels="{checked: $t('integrity.block'), unchecked: $t('integrity.block')}"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'service-item',
  props: {
    service: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      isTooltipVisible: false,
    };
  },
  computed: {
    tooltipText() {
      if (!this.isTooltipVisible) { return false; }
      return this.service.checked ?
      `${this.service.label} ${this.$t('integrity.whitelisted-tip')}` :
      `${this.service.label} ${this.$t('integrity.blocked-tip')}`;
    },
    labelColor() {
      const isAd = this.service.label === this.$t('integrity.ad');
      return isAd ? '#5bc0de' : '#f0ad4e';
    },
  },
  methods: {
    onChange() {
      this.$emit('changeWhitelistDomain');
      this.isTooltipVisible = true;
      setTimeout(() => {
        this.isTooltipVisible = false;
      }, 2000);
    },
  },
};
</script>

<style scoped>
  .service-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 15px 8px 15px;
  }

  .service__toggle {
    display: flex;
    align-items: center;
  }

  .service-info .label {
    margin-right: 5px;
  }

  .service-info {
    display: flex;
    align-items: center;
  }

  .service-info__title {
    flex: 0 0 210px;
    font-weight: bold;
  }

  .service__toggle label {
    margin-bottom: 0px;
  }

  .tooltip-message {
    position: relative;
    background: #283D59;
    color: #fff;
    left: 30px;
    padding: 0 10px;
    border-radius: 10px;
  }
</style>



