<template>
  <img src="../../assets/ovpn-logo.png" :style="styleObj" alt="OVPN" title="OVPN">
</template>

<script>
export default {
  name: 'logo',
  props: {
    styleObj: {
      type: Object,
      required: true,
    },
  },
};
</script>



