import axios from 'axios';
import settings from '../settings/settings';
import local from './local';

const { baseApiUrl } = settings;

const header = `Browser extension v${local.getVersion()}`;
axios.defaults.headers.common['Ovpn-Software'] = header;

export default {
  login(username, password) {
    const loginUrl = `${baseApiUrl}/client`;

    return axios.post(loginUrl, {
      username,
      password,
    })
    /* eslint-disable consistent-return */
    .then((res) => {
      if (res.status === 200) {
        return {
          username,
          ...res.data.user,
        };
      }
    })
    .catch((error) => {
      if (error.response) {
        if (error.response.status === 422) {
          throw new Error(error.response.data.message);
        }
      }
    });
  },

  register(userdata) {
    const registerUrl = `${baseApiUrl}/client/register`;
    const {
      username,
      email,
      password,
      confirmPassword,
      language,
    } = userdata;

    const data = {
      username,
      email,
      password,
      language,
      password_confirmation: confirmPassword,
    };

    return axios.post(registerUrl, data)
    /* eslint-disable consistent-return */
    .then((res) => {
      if (res.status === 200) {
        return res;
      }
    })
    .catch((error) => {
      if (error.response) {
        throw new Error(error.response.data.message);
      }
    });
  },

  getIpData() {
    const ipDataUrl = `${baseApiUrl}/client/ptr`;

    return axios.get(ipDataUrl)
    /* eslint-disable consistent-return */
    .then(res => res)
    .catch(() => {
      throw new Error('Failed to fetch Ip data');
    });
  },

  getServerData(serverIp) {
    const serverDataUrl = `${baseApiUrl}/client/server/${serverIp}`;

    return axios.get(serverDataUrl)
    /* eslint-disable consistent-return */
    .then(res => res)
    .catch(() => {
      throw new Error('Failed to fetch server data');
    });
  },
};



