/* eslint-disable no-undef*/

const keys = {
  user: 'ovpn_user',
  settings: 'ovpn_settings',
  count: 'ovpn_count',
  tabsCounts: 'ovpn_tabs',
  piwik: 'ovpn_piwik',
  sitesWhitelist: 'ovpn_sites_whitelist',
  domainsWhitelist: 'ovpn_domains_whitelist',
  startDate: 'ovpn_start_date',
  version: 'ovpn_version',
  clientVersion: 'ovpn_client_version',
  forceList: 'ovpn_force_list',
  serverData: 'ovpn_server_data',
  ipData: 'ovpn_ip_data',
};

export default {
  // User related
  setUser(user) {
    localStorage.setItem(keys.user, JSON.stringify(user));
  },
  getUser() {
    return JSON.parse(localStorage.getItem(keys.user));
  },
  clearUser() {
    localStorage.removeItem(keys.user);
  },

  // Settings related
  getSettings() {
    return JSON.parse(localStorage.getItem(keys.settings));
  },
  setLocalSettings(settings) {
    localStorage.setItem(keys.settings, JSON.stringify(settings));
  },

  // Counts related
  getTotalCounts() {
    return JSON.parse(localStorage.getItem(keys.count));
  },
  getTabsCounts() {
    return JSON.parse(localStorage.getItem(keys.tabsCounts));
  },

  // Whitelist related
  getSitesWhitelist() {
    return JSON.parse(localStorage.getItem(keys.sitesWhitelist));
  },
  setSitesWhitelist(list) {
    localStorage.setItem(keys.sitesWhitelist, JSON.stringify(list));
  },
  getDomainsWhitelist() {
    return JSON.parse(localStorage.getItem(keys.domainsWhitelist));
  },
  setDomainsWhitelist(list) {
    localStorage.setItem(keys.domainsWhitelist, JSON.stringify(list));
  },

  // Force
  getForceList() {
    return JSON.parse(localStorage.getItem(keys.forceList));
  },
  setForceList(list) {
    localStorage.setItem(keys.forceList, JSON.stringify(list));
  },

  // Piwik related
  setPiwikId() {
    const randomHex = Math.random().toString(16).substr(2, 8) +
      Math.random().toString(16).substr(2, 8);
    localStorage.setItem(keys.piwik, JSON.stringify(randomHex));
  },
  getPiwikId() {
    return JSON.parse(localStorage.getItem(keys.piwik));
  },
  removePiwikId() {
    localStorage.removeItem(keys.piwik);
  },

  // Start of usage
  getStartDate() {
    return JSON.parse(localStorage.getItem(keys.startDate));
  },

  // Version of extension and client
  getVersion() {
    return JSON.parse(localStorage.getItem(keys.version));
  },
  getClientVersion() {
    return JSON.parse(localStorage.getItem(keys.clientVersion));
  },

  // connection data
  getServerData() {
    return JSON.parse(localStorage.getItem(keys.serverData));
  },
  setServerData(data) {
    localStorage.setItem(keys.serverData, JSON.stringify(data));
  },

  getIpData() {
    return JSON.parse(localStorage.getItem(keys.ipData));
  },
  setIpData(data) {
    localStorage.setItem(keys.ipData, JSON.stringify(data));
  },
};



