/* eslint-disable */
import Raven from 'raven-js';
import local from '../../../api/local';
import chromeHelpers from '../../../api/chrome';
import defaultSettings from '../../../data/settingsList';

import {
  SET_START_SETTINGS,
  UPDATE_SETTINGS,
} from './mutation-types';

const state = {
  list: { ...defaultSettings },
};

const mutations = {
  [SET_START_SETTINGS](state, settingsList) {
    state.list = settingsList;
  },
  [UPDATE_SETTINGS](state, settingsList) {
    state.list = settingsList;
  },
};

const actions = {
  setStartSettings({ commit }, settingsList) {
    const localSettings = local.getSettings();
    if (localSettings) {
      commit(SET_START_SETTINGS, localSettings);
    } else {
      local.setLocalSettings(settingsList);
      commit(SET_START_SETTINGS, settingsList);
    }
  },

  updateSettings({ commit }, data) {
    const { value, option } = data;
    const updatedList = { ...state.list };
    updatedList[option].checked = value;
    local.setLocalSettings(updatedList);
    if (option === 'sendData') {
      if (value) {
        Raven.install();
      } else {
        Raven.uninstall();
      }
    }
    chromeHelpers.sendMessage({'changeSetting': data});
    commit(UPDATE_SETTINGS, updatedList);
  },
};

export default {
  state,
  mutations,
  actions,
};



