/* eslint-disable */
import {
  SET_TABS_DATA,
} from './mutation-types';

const state = {
  tabs: {},
};

const mutations = {
  [SET_TABS_DATA](state, data) {
    state.tabs = data;
  },
};

const actions = {
  setTabsData({ commit }, data) {
    commit(SET_TABS_DATA, data);
  },
};

export default {
  state,
  mutations,
  actions,
};



